  height: 100%;
}

body {
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -ms-flex-direction: column;
      flex-direction: column;
}

nav .brand-logo {
  margin-left: 12px;
}

div.fixed-action-btn {
  z-index: 9;
  bottom: 12px;
  right: 12px;
}

iframe.video-demo {
  width: 853px;
  height: 480px;
}

@media only screen and (max-width: 992px) {
  nav .brand-logo {
    max-width: 200px;
    text-overflow: ellipsis;
    overflow: hidden;
    margin-left: 0;
  }

  iframe.video-demo {
    width: 560px;
    height: 315px;
  }
}

@media only screen and (max-width: 560px) {
  iframe.video-demo {
    width: 320px;
    height: 180px;
  }
}




